import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PostsService } from 'src/app/core/posts.service';
import { PostModel } from 'src/app/core/post.model';

@Component({
    selector: 'app-blog-post-page',
    templateUrl: './blog-post.page.html',
    styleUrls: []
})
export class BlogPostPageComponent implements OnInit {

    public post: PostModel = new PostModel();
    public postId;
    public featuredImage;

    public posts = [];

    constructor(
        private route: ActivatedRoute,
        private postService: PostsService,
    ) {
        route.paramMap.subscribe(
            params => {
                this.postId = params.get('id');
            }
        )
    }

    public loadPost( id ) {
        this.postService.getPostById( id ).subscribe(
            post => {
                this.post = post;
                console.log( this.post );
                this.featuredImage = post._embedded['wp:featuredmedia'][0].source_url;
            }
        )
    }

    ngOnInit(): void {
        this.loadPost( this.postId );
        this.postService.getLatestPosts().subscribe(
            posts => {
                this.posts = posts;
                this.posts = this.posts.filter( x => x.id != this.postId );
            }
        )
    }
}
