import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PostModel } from './post.model';
import { Observable } from 'rxjs';
import { ServiceBase } from './service-base';

@Injectable()
export class PostsService extends ServiceBase {

    constructor(
        private http: HttpClient
    ) {
        super();
    }

    public getLatestPosts(): Observable<PostModel[]> {
        return this.http.get<PostModel[]>( this.getApiUrl('posts/?_embed') );
    }

    public getPostById( id ): Observable<PostModel> {
        return this.http.get<PostModel>( this.getApiUrl('posts/' + id + '?_embed'));
    }
}