export class RenderedModel {
    rendered = '';
}

export class EmbeddedModel {
    public 'wp:featuredmedia';
}

export class PostModel {
    id;
    date;
    date_gmt;
    guid;
    slug;
    status;
    type;
    link;
    title: RenderedModel;
    content: RenderedModel;
    excerpt: RenderedModel;
    _embedded: EmbeddedModel;

    get excerptString() {
        const plainText = this.excerpt.rendered.replace(/<[^>]*>/g, '');
        return plainText;
    }

    public featuredImageSrc() {
        return this._embedded['wp:featuredmedia'][0].source_url;
    }
}