import { Component, OnInit } from '@angular/core';
import { PostsService } from 'src/app/core/posts.service';
import { PostModel } from 'src/app/core/post.model';
import { HomeSliderService } from 'src/app/core/home-slider.service';
import Glide from 'node_modules/@glidejs/glide';
import 'scripts/jquery.particleground.js';
declare var particleground:any;

@Component({
    selector: 'app-home-page',
    templateUrl: './home.page.html',
    styleUrls: []
})
export class HomePageComponent implements OnInit {

    public posts: PostModel[] = [];

    constructor(
        private postsService: PostsService,
        private homeSliderService: HomeSliderService,
    ) { }

    ngOnInit(): void {
        this.postsService.getLatestPosts().subscribe(
            posts => {
                this.posts = posts.slice(0, 4);
                console.log( posts );
            }
        )

        this.homeSliderService.getHomeSliderPosts().subscribe(
            (posts: PostModel[]) => {
                console.log("Home slider");
                console.log( posts[0]._embedded['wp:featuredmedia'][0].source_url );
            }
        )

        document.querySelectorAll('.glide').forEach(
            (element: HTMLElement) => {
                const elementId = element.id;
                const gapOption = element.dataset.gap || null;
        
                new Glide( '#' + elementId, {
                    autoplay: 3000,
                    hoverpause: true,
                    rewind: true,
                }).mount()
            }
        )

        if( document.getElementById('particles-background') ) {
            particleground(document.getElementById('particles-background'), {
                parallax: false,
                dotColor: 'rgba(255,255,255,0.4)',
                lineColor: 'rgba(255,255,255,0.3)',
            });
        }
    }
}
