import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomePageComponent } from './pages/home/home.page';
import { ContactUsPageComponent } from './pages/contact-us/contact-us.page';
import { SolutionsPageComponent } from './pages/solutions/solutions.page';
import { AboutUsPageComponent } from './pages/about-us/about-us.page';
import { ClientsPageComponent } from './pages/clients/clients.page';
import { BlogPageComponent } from './pages/blog/blog.page';
import { BlogPostPageComponent } from './pages/blog-post/blog-post.page';

const routes: Routes = [
    { path: '', component: HomePageComponent },
    { path: 'contato', component: ContactUsPageComponent },
    { path: 'quem-somos', component: AboutUsPageComponent },
    { path: 'solucoes', component: SolutionsPageComponent },
    { path: 'solucoes/:name', component: SolutionsPageComponent },
    { path: 'clientes', component: ClientsPageComponent },
    { path: 'blog', component: BlogPageComponent },
    { path: 'blog/:id/:slug', component: BlogPostPageComponent }
    // { path: 'path', component: FeatureComponent },
    // { path: '**', component: PageNotFoundComponent },
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {}
